#masthead {
	background-color: green;
}

.menu-toggle {
	position: relative;
	width: 30px;
	height: 20px;
	cursor: pointer;

	span {
		display: block;
		position: absolute;
		height: 4px;
		width: 100%;
		background-color: #333;
		border-radius: 2px;
		transition: all 0.3s ease;

		&::before,
		&::after {
			content: '';
			position: absolute;
			height: 4px;
			width: 100%;
			background-color: #333;
			border-radius: 2px;
			transition: all 0.3s ease;
		}

		&::before {
			top: -8px;
		}

		&::after {
			bottom: -8px;
		}
	}

	&.active span {
		background-color: transparent;

		&::before {
			transform: translateY(8px) rotate(45deg);
		}

		&::after {
			transform: translateY(-8px) rotate(-45deg);
		}
	}
}
